.subscriptions {
  h4 {
    margin-top: 15px;
    text-align: center;
    font-size: 17px;
    font-family: "Exo 2";
    color: #333;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    margin: 0 10px; }

  a {
    text-decoration: none; } }

.channel-img__contaniner {
  background: #FFF;
  margin-bottom: 15px;
  padding: 5px;
  border-radius: 100%;
  overflow: hidden;
  img {
    width: 100%;
    border-radius: 100%; }
  &:hover {
    @include shadow-8dp; } }
