button.playpause {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  border: none;
  cursor: pointer;
  // background: $accent-color
 }  // color: $primary-color-text

.episodes-list {
  max-width: 95%;
  width: 100%;
  margin: 0 auto;
  @include respond-to($flex-start-width) {
    max-width: 80%; } }

ul.episodes {
  padding: 0;
  margin: 0;
  list-style: none;
  li.episode {
    padding: 0;
    font-size: 16px;
    border-bottom: 1px solid #CCC;
    &:hover {
      background: #eee; }
    &.is-played {
      h3.title, .button-play-wrapper, time, .more-itens > button {
        opacity: .5; } } }
  .head {
    display: flex;
    justify-content: space-between;
    padding: 10px 5px;
    @include respond-to($flex-start-width) {
      padding: 20px; } }
  .body {
    display: none;
    border-top: 1px dashed #bbb;
    line-height: 1.5;
    padding: 10px 5px;
    &.open {
      display: block; }
    @include respond-to($flex-start-width) {
      padding: 20px; }
    a {
      display: inline; } }

  h3.title {
    margin: 0;
    padding: 0 10px;
    line-height: 1.5;
    font-weight: 700;
    flex: 1 1 auto;
    align-self: center;
    font-weight: normal;
    font-size: 16px; }
  a {
    display: block;
    // color: $primary-text-color
    &:hover {
      text-decoration: none; } }
  time {
    flex: 0 0 auto;
    align-self: center;
    font-size: 12px;
    margin: 0 10px; }

  .more-itens {
    flex: 0 0 auto;
    align-self: center;
    position: relative;
    button {
      font-size: 20px;
      cursor: pointer;
      text-align: center;
      border: none;
      background: transparent;
      height: 40px;
      color: #333; } }

  .itens {
    position: absolute;
    background-color: #FFF;
    top: 0;
    right: 0;
    border-radius: 3px;
    overflow: hidden;
    width: 160px;
    z-index: 2;
    opacity: 0;
    @include shadow-2dp;
    transform: scale(0) translateZ(0);
    transform-origin: top right;
    &.open {
      transform: scale(1) translateZ(0);
      opacity: 1; }
    ul {
      margin: 0;
      padding: 0;
      font-family: normal;
      list-style: none;
      line-height: 32px; }
    li {
      &:last-child button {
        border-bottom: none; } }
    button {
      // border-bottom: 1px solid $divider-color
      padding: 0 15px;
      width: 100%;
      text-align: left;
      font-size: 15px;
      cursor: pointer;
      &:hover {
        // background: $divider-color
        color: #333; } } }

  .button-play-wrapper {
    flex: 0 0 auto;
    align-self: center; }

  #loading-episodes {
    text-align: center;
    display: block; } }
