html, body {
   min-height: 100%;
   background: #F0F4F8;
   color: #212121; }

.mdl-layout__content {
  padding-bottom: 120px; }

.liquid-modal {
  .liquid-child {
    position: absolute;
    z-index: 2;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0; }

  .lf-dialog {
    border: none;
    padding: 0;
    @include shadow-2dp;
    @extend .mdl-card; } }

img.channel-img {
  z-index: 99; }

.uhura__logo {
  font-weight: 900;
  font-family: "Exo 2";
  color: #FF5722; }

.mdl-layout-title.uhura__logo {
  font-size: 30px;
  color: rgb(51, 51, 51);
  line-height: 2; }

*, *:after, *:before {
  transition-property: box-shadow, border-radius;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1); }

